@tailwind base;
@tailwind components;
@tailwind utilities;

html, body {
 overflow: hidden
}

.active {
 @apply text-white;
}

.active svg {
 @apply text-white;
}

.active .playlist {
 @apply bg-[#222222]
}

.active .artist {
 @apply bg-[#222222]
}

.bar:nth-child(2) {
 animation-delay: 0.2s;
}

.bar:nth-child(3) {
 animation-delay: 0.4s;
}

.bar:nth-child(4) {
 animation-delay: 0.6s;
}

.bar:nth-child(5) {
 animation-delay: 0.8s;
}

.bar:nth-child(6) {
 animation-delay: 1s;
}

.smooth-transition {
 transition: all 0.3s ease-in-out;
}

::-webkit-scrollbar {
 width: 10px;
}

::-webkit-scrollbar-track {
 background: #1f1f1f;
}

::-webkit-scrollbar-thumb {
 background: #6b6b6b;
}

::-webkit-scrollbar-thumb:hover {
 background: #7e7e7e;
}

.hide-scrollbar::-webkit-scrollbar {
 display: none;
}

.hide-scrollbar {
 -ms-overflow-style: none;
 scrollbar-width: none;
}